@import '../base.scss';

.mainTitle {
    @include font(48px, 56px, null, bold, null);

    &_extra {
        @include font(56px, 60px, null, null, null);
    }
}

.mainTitle_extra {
    @extend .mainTitle;
    @include font(56px, 60px, null, null, null);
}

.secondaryTitle {
    @include font(36px, 48px, null, bold, null);
}

.secondaryTitle_extra {
    @extend .secondaryTitle;
    font-weight: normal;
}