@import '../../base.scss';

.navStep {
    display: block;
    overflow: hidden;
    position: relative;
    opacity: 0.25;
    cursor: default;
    padding-left: 0;
    $trans: font-weight 0.3s, opacity 0.3s, color 0.3s, padding-left 0.3s;
    @include transition($trans);

    &+.navStep {
        margin-top: 16px;
    }

    &:before {
        content: '\e901';
        display: block;
        position: absolute;
        font-family: $font2;
        font-size: 24px;
        line-height: 24px;
        font-weight: normal;
        left: -24px;
        top: 50%;
        margin-top: -12px;
        opacity: 0;
        $trans: opacity 0.3s, left 0.3s;
        @include transition($trans);
    }

    &_active {
        @extend .navStep;
        font-weight: bold;
        color: $primaryColor;
        padding-left: 30px;
        opacity: 1;

        &:before {
            opacity: 1;
            left: 0;
        }
    }

    &_finished {
        @extend .navStep;
        opacity: 0.66;
        padding-left: 30px;

        &:before {
            opacity: 1;
            left: 0;
        }

        &:hover {
            opacity: 1;
            color: $primaryColor;
        }
    }
}