@import '../../base.scss';

.stepScreen {
    @include flexbox;
    @include align-items(stretch);
    min-height: 100vh;
    width: calc(100% + 20px);
    background-color: $backMain;

    .stepScreenLeft_container {
        width: 400px;
        padding: 96px 60px 48px;
        @include flexbox;
        background-color: $whiteColor;
    }

    .stepScreenRight_container {
        width: calc(100% - 400px);
        background-color: $backMain;
        padding: 180px 160px 160px;
    }
}