@import '../../../base.scss';

.secretSeedTabItem {
    @include flexbox;
    height: 120px;
    @include align-items(center);
    position: relative;
    padding: 15px 15px 15px 120px;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid $borderColorDefault;
    $trans: background-color 0.3s, border-color 0.3s;
    @include transition($trans);

    &:before {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        border: 3px solid $borderColorActive;
        opacity: 0;
        $trans: opacity 0.3s;
        @include transition($trans);
    }

    .caption {
        font-weight: bold;
    }

    &:after {
        content: '\e902';
        display: block;
        position: absolute;
        font-family: $font2;
        right: -12px;
        top: -12px;
        z-index: 1;
        width: 36px;
        @include font(24px, 36px, $whiteColor, null, null);
        text-align: center;
        @include border-radius(50%);
        background-color: $primaryColor;
        @include transform(scale(00));
        @include transition(transform 0.3s);
    }

    .icon_container {
        position: absolute;
        width: 120px;
        top: 0;
        left: 0;
        @include font(120px, 120px, $iconColorDefault, null, null);
        @include transition(color 0.3s);

        .icon {
            display: block;
        }
    }

    &:hover {
        border-color: $borderColorActive;

        &:before {
            opacity: 1;
        }
    }

    &_active {
        @extend .secretSeedTabItem;
        background-color: $whiteColor;
        border-color: $borderColorActive;

        &:before {
            opacity: 1;
        }

        .icon_container {
            color: $iconColorActive;
        }

        &:after {
            @include transform(scale(1));
        }
    }
}