$font1: 'Alegreya Sans';
$font2: 'icomoon';
$font3: 'Nunito';

$whiteColor: #fff;
$blackColor: #000;

$textColor: #000;
$textColorDefault: rgba($textColor, 0.4);

$primaryColor: #0073ff;

$buttonDefaultBack: #000;
$buttonBackHover: #042b5b;
$buttonPrimaryBackHover: rgba(#042b5b, 0.2);

$backMain: #f7f8fc;

$borderColorDefault: rgba(#000, 0.2);
$borderColorActive: $primaryColor;
$chipBorderColor: #e4ebf9;

$inputTextColor: rgba($textColor, 0.9);
$inputBorderHover: rgba($blackColor, 0.66);
$inputBorderFocus: rgba(#182b42, 0.66);
$inputLabelFocus: rgba($blackColor, 0.66);

$iconColorDefault: #5a5a5a;
$iconColorActive: $primaryColor;

$popoverButtonBorder: rgba(#042b5b, 0.1);

$thumbShadowColor: #00469a;

:export {
    fontPrimary: $font1;
    fontSecondary: $font3;
    primaryColor: $primaryColor;
    whiteColor: $whiteColor;
    buttonDefaultBack: $buttonDefaultBack;
    buttonBackHover: $buttonBackHover;
    backMain: $backMain;
    borderColorDefault: $borderColorDefault;
    textColorDefault: $textColorDefault;
    inputTextColor: $inputTextColor;
    inputBorderHover: $inputBorderHover;
    inputLabelFocus: $inputLabelFocus;
    inputBorderFocus: $inputBorderFocus;
    chipBorderColor: $chipBorderColor;
    thumbShadowColor: $thumbShadowColor;
}