@import '../base.scss';

.popoverButton {
    display: block;
    cursor: pointer;
    width: 36px;
    text-align: center;
    @include border-radius(50%);
    border: 2px solid $popoverButtonBorder;
    @include font(16px, 32px, $textColorDefault, null, null);
    $trans: border-color 0.3s, color 0.3s;
    @include transition($trans);

    &:hover {
        border-color: $primaryColor;
        color: $primaryColor;
    }
}