@import '../../base.scss';

.startScreen {
    @include flexbox;
    @include align-items(stretch);
    min-height: 100vh;

    .startScreenLeft_container,
    .startScreenRight_container {
        width: 50%;
    }

    .startScreenLeft_container {
        padding: 180px 60px 48px;
        @include flexbox;
        color: $whiteColor;
        background-image: url(../../assets/img/start_background.svg);
        @include back-img(cover, center, no-repeat);
    }

    .startScreenRight_container {
        padding: 192px 160px 160px;
        color: $textColor;
    }
}