@import '../../../base.scss';

.requestsCounterWrapper {
    @include flexbox;
    @include flex-wrap(wrap);
    margin: -20px -10px 0;

    .requestsCounterItem_container {
        padding: 20px 10px 0;
    }

    .requestsCounterItem {
        display: block;
        background-color: $whiteColor;
        border: 1px solid $chipBorderColor;
        width: 54px;
        text-align: center;
        @include font(28px, 62px, $inputLabelFocus, null, null);
        cursor: pointer;
        @include border-radius(4px);
        @include box-shadow(inset 0px 0px 0px 2px transparent);
        $trans: box-shadow 0.3s, background-color 0.3s, color 0.3s, border-color 0.3s;
        @include transition($trans);

        &:hover {
            color: $textColor;
            @include box-shadow(inset 0px 0px 0px 2px $primaryColor);
        }

        &_active {
            @extend .requestsCounterItem;
            background-color: $primaryColor;
            border-color: $primaryColor;
            color: $whiteColor;
            cursor: default;

            &:hover {
                color: $whiteColor;
            }
        }
    }
}