@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?pamstb');
  src:  url('fonts/icomoon.eot?pamstb#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?pamstb') format('truetype'),
    url('fonts/icomoon.woff?pamstb') format('woff'),
    url('fonts/icomoon.svg?pamstb#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-close:before {
  content: "\e907";
}
.icon-question:before {
  content: "\e906";
}
.icon-arrow-left:before {
  content: "\e905";
}
.icon-check:before {
  content: "\e902";
}
.icon-node-full:before {
  content: "\e903";
}
.icon-validator:before {
  content: "\e904";
}
.icon-arrow-right:before {
  content: "\e901";
}
.icon-logo:before {
  content: "\e900";
}
