@import '../base.scss';

.thumbShadow {
    display: block;
    position: absolute;
    z-index: -1;
    width: 25px;
    height: 25px;
    @include border-radius(50%);
    opacity: 0.5;
    top: -9px;
    left: 50%;
    margin-left: -12.5px;
    -webkit-filter: blur(20px);
    filter: blur(20px);
    background-color: $thumbShadowColor;
}