@import '../base.scss';

.popoverInputWrapper {
    position: relative;

    .buttonPopover_container {
        position: absolute;
        right: 6px;
        top: -43px;
        margin-top: 0;
    }

    .MuiInput-formControl {
        padding: 0 56px 23px 0;
    }
}

.btnClosePopover_container {
    display: block;
    position: absolute;
    cursor: pointer;
    bottom: 12px;
    right: 12px;
    opacity: 0.5;
    @include font(24px, 24px, $whiteColor, null, null);
    @include transition(opacity 0.3s);

    &:hover {
        opacity: 1;
    }
}