@import '../base.scss';

.mainButton {
    display: table;
    position: relative;
    min-width: 220px;
    font-family: $font3;
    @include font(20px, 30px, $whiteColor, bold, null);
    cursor: pointer;
    background-color: $buttonDefaultBack;
    @include transition(opacity 0.3s);

    &:before {
        content: '';
        display: block;
        position: absolute;
        z-index: 1;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: $buttonBackHover;
        opacity: 0;
        @include transition(opacity 0.3s);
    }

    .buttonInner {
        @include flexbox;
        @include align-items(center);
        height: 56px;
        padding: 5px 24px 5px;
        position: relative;
        z-index: 1;

        &_buttonPrev {
            @extend .buttonInner;
            padding-left: 47px;
            $trans: padding-right 0.3s, padding-left 0.3s;
            @include transition($trans);

            &:after {
                content: '\e905';
                display: block;
                position: absolute;
                font-family: $font2;
                @include font(24px, 24px, inherit, normal, null);
                top: 50%;
                margin-top: -12px;
                left: 0;
            }
        }

        &_buttonNext {
            @extend .buttonInner;
            padding-right: 56px;

            &:after {
                content: '\e901';
                display: block;
                position: absolute;
                font-family: $font2;
                @include font(24px, 24px, inherit, normal, null);
                top: 50%;
                margin-top: -12px;
                right: 16px;
                @include transition(right 0.3s);
            }
        }
    }

    &:hover {
        &:before {
            opacity: 1;
        }

        .buttonInner {
            &_buttonNext {
                &:after {
                    right: 12px;
                }
            }

            &_buttonPrev {
                padding-left: 35px;
                padding-right: 36px;
            }
        }
    }

    &_buttonPrimary {
        @extend .mainButton;
        background-color: $primaryColor;

        &:hover {
            &:before {
                opacity: 0.2;
            }
        }
    }

    &_buttonText {
        @extend .mainButton;
        background-color: transparent;
        color: $textColor;
        opacity: 0.5;
        min-width: 0;

        &:hover {
            opacity: 1;

            &:before {
                opacity: 0;
            }
        }
    }
}